* {
  box-sizing: border-box;
  outline: none;
  -webkit-overflow-scrolling: touch; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }


// REMOVE FOR PRODUCTION !
.dev-bp-debug {
  background: black;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
  color: white;
  padding: 5px;
  font-size: 12px;
  border-bottom-left-radius: 5px; }
