#widget .cb__checkbox {
  position: relative;
  font-size: 0;
}

#widget .cb__checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

#widget .cb__checkbox input:checked + label::after {
  transform: translateY(-50%) scale(1);
}

#widget .cb__checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}

#widget .cb__checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 7px;
  border: 1px solid white;
  border-radius: 2px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  opacity: .5;
  transition: all 0.25s ease-in-out;
}

#widget .cb__checkbox label::after {
  position: absolute;
  left: 3.5px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  display: inline-block;
  content: ' ';
  size: 2px;
  opacity: .5;
  background: white;
  border-radius: 1px;
  transition: transform .2s ease;
}

#widget .cb__checkbox label span {
  display: inline-block;
  padding-left: 13px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 10px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}

#widget .cb__checkbox label span a {
  position: relative;
  display: inline-block;
  opacity: .5;
  color: white;
  transition: 0.25s ease-in-out;
}

#widget .cb__checkbox label span a::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.5);
  height: 1px;
  transition: 0.25s ease-in-out;
}

#widget .cb__checkbox label span a:hover {
  opacity: 1;
}

#widget .cb__checkbox label span a:hover::after {
  right: 100%;
}

